﻿.index-lunbo {
	margin: 0 !important;
}

	.index-lunbo .am-control-nav {
		bottom: 20px !important;
	}

	.index-lunbo .am-nav-prev .am-prev {
		left: 0;
		width: 50px;
		height: 150px;
		background: #1a2229;

		&::before {
			content: "\e78e";
			color: #fff;
			height: 150px;
			font-family: "iconfont";
			line-height: 150px !important;
			font-size: 3.2rem !important;
		}
	}




	.index-lunbo .am-nav-next .am-next {
		right: 0;
		width: 50px;
		height: 150px;
		background: #1a2229;

		&::before {
			content: "\e625" !important;
			color: #fff;
			height: 150px;
			font-family: "iconfont";
			line-height: 150px !important;
			font-size: 3.2rem !important;
		}
	}



	.index-lunbo .am-control-nav li {
		margin: 0 10px;

		a {
			width: 12px;
			height: 12px;
		}

			a.am-active {
				background-color: #feffff;
				cursor: default;
			}
	}



.notice {
	.cg {
		padding: 1.5rem;
		overflow: hidden;
		background: #f5f5f5;

		.am-slider-default {
			margin-bottom: 0;
		}

		.am-nav-prev .am-prev {
			left: 0;
			width: 25px;
			background: #1a2229;
			height: 100%;
			top: 18px;

			&::before {
				content: "\e78e";
				font-family: "iconfont";
				font-size: 2.3rem !important;
				position: absolute;
				height: 50%;
				top: 25%;
				display: flex;
				align-items: center;
				color: #fafaf8;
			}

			&:hover {
				opacity: 0.8;
			}
		}

		.am-nav-next .am-next {
			right: 0;
			width: 25px;
			height: 100%;
			top: 18px;
			background: #1a2229;

			&::before {
				content: "\e625";
				font-family: "iconfont";
				font-size: 2.3rem !important;
				position: absolute;
				height: 50%;
				top: 25%;
				display: flex;
				align-items: center;
				color: #fafaf8;
			}

			&:hover {
				opacity: 0.8;
			}
		}

		& > div {
			padding: 0;
		}

		ol {
			display: none;
		}
	}

	&-left {
		&-slider {
			padding: 1rem 0;
			background: #050505;
		}

		&-img {
			img {
				width: 100%;
			}
		}
	}

	&-right {

		&-div {
			margin-left: 2rem;
			background: #fff;
			border: 1px solid #eee;

			h1 {
				border-bottom: 1px solid #eee;
			}

			ul {
				padding: 2rem 3rem;

				li {
					width: 100%;
					padding: 0.5rem;
					position: relative;

					&::after {
						position: absolute;
						left: -15px;
						top: 2px;
						content: "\e600";
						color: #0076ca;
						font-size: 2rem;
					}

					&:hover span {
						color: @hover-color;
					}


					span:extend(.f-toe) {
						display: block;
						width: 100%;
					}
				}
			}


			img {
				display: block;
				width: 100%;
				margin: 0;
			}

			strong:extend(.f-toe) {
				background: #0076ca;
				display: block;
				font-weight: 500;
				padding: 0.8rem 0.8rem 0.8rem 2rem;
				color: #fff;

				a {
					color: #fff;
				}
			}
		}
	}
}

.index-category {
	overflow: hidden;

	.cg > div {
		padding: 0;


		&:first-child {
			padding-right: 0.8rem;
		}

		&:nth-child(2) {
			padding: 0 0.8rem;
		}

		&:last-child {
			padding-left: 0.8rem;
		}

		& > div {
			border: 1px solid #f5f5f5;
			height: 435px;
			overflow: hidden;
		}

		ul.comcategory {
			width: 100%;
			margin: auto;
			overflow: hidden;
			padding: 1rem 0;

			li {
				padding: 0 3rem;
				display: block;
				position: relative;
				overflow: hidden;

				&::after {
					position: absolute;
					left: 9px;
					top: -3px;
					content: "\e600";
					font-size: 2rem;
					color: #0076ca;
				}

				span:extend(.f-toe) {
					display: block;
					color: #494949;
					font-size: 1.4rem;
					float: left;
				}

				i {
					color: #b5b5b5;
					font-size: 1.2rem;
					float: left;
					display: block;
					width: 100%;
				}

				&:hover {
					span {
						color: @hover-color;
					}

					i {
						color: #878686;
					}
				}
			}
		}
	}



	&-left {
		img {
			width: 100%;
			margin: auto;
			display: block;
			padding: 0 1.5rem;
		}
	}

	&-right {
		.am-nav-tabs, .am-nav-tabs li a, .am-nav-tabs li a:focus, .am-tabs-bd {
			border: 0;
		}

			.am-nav-tabs li {
				width: 50%;
				text-align: center;

				a {
					padding: 1rem 0;

					&:hover {
						border: 0;
					}
				}

				&.am-active {
					background: #f5f5f5;

					a {
						background: #f5f5f5;
					}
				}
			}

		.am-tab-panel ul {
			li {
				padding-left: 1rem;
			}
		}
	}
}

.index-sliderbottom {
	overflow: hidden;

	& > div {
		border-bottom: 1px solid #e9e9e9;
		overflow: hidden;
		padding-bottom: 2rem;

		& ol {
			display: none;
		}

		& .am-slider-default {
			box-shadow: none;
			margin: 0 3rem;
			margin-top: 1rem;

			.am-direction-nav .am-prev {
				left: -4rem;

				&::before {
					content: "\e78e";
					font-family: "iconfont";
					font-size: 3rem !important;
					color: #898888;
				}
			}

			.am-direction-nav .am-next {
				right: -4rem;

				&::before {
					content: "\e625";
					font-family: "iconfont";
					font-size: 3rem !important;
					color: #898888;
				}
			}
		}

		a:hover span {
			color: @hover-color;
		}


		& li span:extend(.f-toe) {
			display: block;
			color: #666;
			font-size: 1.4rem;
			padding: 0.6rem;
		}
	}
}

@media only screen and (max-width:@breakpoint-medium-max) {

   .index-lunbo .am-nav-prev .am-prev, .index-lunbo .am-nav-next .am-next {
		display: none;
	}
	.index-lunbo .am-control-nav li {
		margin: 0 6px;

		a {
			width: 8px;
			height: 8px;
		}
	}
	.notice-right-div {
		margin:2rem 0 0 0;
	}


	.ads  {
		padding:0!important;
		margin-bottom:2rem;
	}

	.index-category .cg > div {
		padding: 1rem;

		& > div {
			height: auto;
		}
	}

}

