﻿@font-face {
	font-family: 'iconfont';
	src: url('../fonts/iconfont.eot');
	src: url('../fonts/iconfont.eot?#iefix') format('embedded-opentype'), url('../fonts/iconfont.woff') format('woff'), url('../fonts/iconfont.ttf') format('truetype'), url('../fonts/iconfont.svg#iconfont') format('svg');
}

.font {
	font-family: "iconfont" !important;
	font-size: 16px;
	font-style: normal;
	-webkit-font-smoothing: antialiased;
	-webkit-text-stroke-width: 0.2px;
	-moz-osx-font-smoothing: grayscale;
}

.f-btn:active {
	background-image: none;
}

.f-btn {
	display: inline-block;
	margin-bottom: 0;
	font-size: 1.6rem;
	font-weight: 400;
	line-height: 1.2;
	text-align: center;
	white-space: nowrap;
	background-image: none;
	border: 1px solid transparent;
	border-radius: 0;
	cursor: pointer;
	outline: 0;
	-webkit-appearance: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	-webkit-transition: background-color .3s ease-out,border-color .3s ease-out;
	transition: background-color .3s ease-out,border-color .3s ease-out;
}


b, strong, h1, h2, h3, h4, h5 {
	font-weight: 500;
	padding: 0;
	margin: 0;
}

a:active, a:focus {
	outline: 0;
}

*::before, *::after {
	font-family: 'iconfont';
}

i, em {
	font-style: normal;
}

i {
	font-family: 'iconfont';
}

li {
	list-style-type: none;
}

ul {
	margin: 0;
	padding: 0;
}


.cg {
	max-width: 1100px;
	margin: 0 auto;
	width: 100%;
}

.cgs {
	margin: 0 auto;
	width: 100%;
	max-width: 800px;
}

.f-margin10 {
	margin: 1rem 0;
}

.f-l {
	float: left;
}

.f-r {
	float: right;
}

.f-toe {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	-o-text-overflow: ellipsis;
	-icab-text-overflow: ellipsis;
	-khtml-text-overflow: ellipsis;
	-moz-text-overflow: ellipsis;
	-webkit-text-overflow: ellipsis;
}


.hd {
	width: 100%;
	float: left;

	&-top {
		background: #efefef;
		overflow: hidden;
		padding: 10px 0;

		&-left {
			float: left;
		}

		&-right {
			float: right;

			& a:last-child {
				margin-right: 0;
			}
		}

		a {
			color: #5a5a5a;
			margin-right: 1.2rem;

			&:hover {
				color: @hover-color;
			}
		}
	}

	&-search {
		padding: 3rem 0 2rem 0;
		overflow: hidden;

		&-left {
			float: left;
			display: inline-block;
		}

		&-right {
			float: right;
			display: inline-block;

			&-input {
				input {
					height: 3.8rem;
					width: 400px;
					padding: 0 0.8rem;
					border: 2px solid #0076ca;
					border-top-left-radius: 2px;
					border-bottom-left-radius: 2px;
					float: left;
				}

				button {
					float: left;
					height: 3.8rem;
					line-height: 3.8rem;
					background: #0076ca;
					color: #fff;
					border: 1px solid #0076ca;
					font-size: 2.5rem;
					border-top-right-radius: 3px;
					border-bottom-right-radius: 3px;
				}
			}

			&-keyword {
				padding: 0.5rem 0;
				float: left;
				color: #b5b5b6;

				a {
					color: #b5b5b6;
					padding: 0 1.2rem;
					position: relative;

					&::after {
						position: absolute;
						content: "\e603";
						top: 0px;
						right: -12px;
						font-size: 2rem;
					}

					&:last-child::after {
						content: "";
					}

					&:first-child {
						padding-left: 0.6rem;
					}

					&:hover {
						color: @hover-color;

						&::after {
							color: #b5b5b6;
						}
					}
				}
			}
		}

		& > button {
			background: 0 0;
			border: 0;
			font-size: 24px;
			color: #0076ca;
			padding: 0;
			float: right;
			height: 30px;
			line-height: 30px;
		}
	}

	&-bottom {
		border-bottom: 4px solid #0076ca;
		overflow: hidden;

		&-nav {
			margin-right: 1rem;
			padding-top: 1rem;

			li {
				display: inline-block;
				padding-right: 6rem;


				a {
					color: #333;
					font-size: 1.8rem;

					&:hover {
						color: @hover-color;
					}
				}

				&.on a {
					color: @hover-color;
				}
			}

			float: right;
		}

		&-category {
			float: left;
			display: inline-block;

			h1 {
				background: #0076ca;
				padding: 1rem 0;
				float: left;
				margin: 0;
				width: 200px;
				text-align: center;

				i {
					color: #fff;
					float: right;
					height: 30px;
					line-height: 50px;
					margin-right: 12px;
				}

				a {
					font-size: 2rem;
					color: #fff;
					font-weight: 500;
					letter-spacing: 1px;
				}
			}
		}
	}
}

.hd1 {
	width: 100%;
	float: left;
	border: 1px solid #e1dede;
	padding: 4rem 0;

	&-left {
		float: left;

		img {
			width: 100%;
		}
	}

	&-right {
		float: right;

		a {
			border-radius: 18px;
			-webkit-transition: background-color .3s ease-out,border-color .3s ease-out;
			transition: background-color .3s ease-out,border-color .3s ease-out;

			&:first-child {
				background: #0076ca;
				color: #fff;
				padding: .5rem 3rem 0.7rem 3rem;
				border: 1px solid #0076ca;
				margin-right: 2rem;

				&:hover {
					background: #0166ae;
				}
			}

			&:last-child {
				padding: .5rem 3rem 0.7rem 3rem;
				background: #fff;
				color: #0076ca;
				border-radius: 18px;
				border: 1px solid #0076ca;

				&:hover {
					background: #f6f9fb;
				}
			}
		}
	}
}

.banner-slide {
	position: absolute;
	width: 100%;
	height: 100%;

	& > .cg {
		position: relative;
		height: 100%;
		overflow: hidden;

		.category {
			position: absolute;
			left: 0;
			top: 0;
			z-index: 999;
			background: rgba(0,0,0,0.3);
			width: 200px;
			padding: 1rem 0;
			height: 100%;

			li {
				width: 50%;
				float: left;
				text-align: center;
				padding: 0.6rem 0;

				a {
					color: #f0f1f2;

					&:hover {
						color: @hover-color;
					}
				}
			}
		}
	}
}

.banner-slide1 {
	position: relative;
	float: left;
	width: 100%;

	& .cg {
		position: relative;
	}

	&-div {
		position: absolute;
		top: 0;
		left: 0;

		& ul {
			width: 200px;
			background: rgba(0,0,0,0.5);
			position: absolute;
			padding: 1rem 0;
			left: 0px;
			top: 0px;
		}

		& li {
			width: 50%;
			float: left;
			text-align: center;
			padding: .6rem 0;
		}

		& a {
			color: #f0f1f2;

			&:hover {
				color: @hover-color;
			}
		}
	}
}

.ads {
	padding: 1em 0;
	clear: both;

	img {
		width: 100%;
	}
}

.comh1 {
	font-size: 1.8rem;
	padding: 0.8rem;
	color: #303030;

	em {
		float: right;

		b {
			float: left;
			color: #0076ca;
			font-size: 1.5rem;
		}

		i {
			font-size: 2.5rem;
			height: 30px;
			line-height: 31px;
			margin-left: -1px;
			color: #0076ca;
		}
	}
}

.comarticle {
	.cg > div {
		padding: 0;
	}

	&-left {
		margin-right: 1.5rem;
		border: 1px solid #dedede;
		padding: 1rem;

		h1 {
			font-size: 1.8rem;
			padding-left: 2rem;
		}

		ul {
			margin: 0 3rem;
			padding: 1rem 0 2rem 0;
		}

		li {
			padding: 0.8rem;
			border-bottom: 1px dotted #dedede;

			&.on a {
				color: @hover-color;
			}

			&:hover a {
				color: @hover-color;
			}
		}
	}

	&-right {
		margin-left: 1.5rem;
		border: 1px solid #dedede;
		padding: 1rem;

		& > h1 {
			font-size: 1.8rem;
			padding-left: 2rem;
			padding-bottom: 1rem;
			border-bottom: 1px dotted #dedede;
		}

		ul {
			margin: 0 1rem;

			li {
				padding: 1rem;
				border-bottom: 1px dotted #dedede;

				& span {
					display: block;
					font-size: 1.4rem;
					height: 6rem;
					line-height: 2rem;
					overflow: hidden;
				}

				& strong {
					display: block;
					color: #0076ca;
				}

				& h1 {
					margin-top: 1rem;

					& em {
						margin-right: 2rem;
						font-size: 1.4rem;
					}
				}
			}

			& h1 {
				font-size: 1.6rem;
			}
		}
	}
}


.footer {
	margin: 1rem 0 3rem 0;
	overflow: hidden;

	h1 {
		font-size: 1.8rem;
	}

	&-div1 {
		&-top {
			overflow: hidden;
			margin: 1rem 0;

			img {
				box-shadow: 0px 0px 2px 2px rgba(0, 0, 0,0.15);
				border: 1px solid #979595;
				border-radius: 5px;
				width: 80px;
				height: 80px;
				display: inline-block;
				float: left;
			}

			span {
				margin-right: -8rem;
				padding-top: 1rem;
				padding-left: 1rem;
				float: left;
				color: #a3a3a3;

				& em {
					margin-right: -8rem;
				}
			}
		}

		&-bottom {
			color: #a3a3a3;
			line-height: 24px;
			max-height: 72px;
			overflow: hidden;
		}

		& > em a {
			color: #a3a3a3;
			margin: 1rem 0 2rem 0;
			display: block;

			&:hover {
				color: @hover-color;
			}
		}
	}

	&-div2 {
		ul {
			margin-top: 1rem;
		}

		li {
			padding: 0.5rem 0;
			position: relative;

			a:extend(.f-toe) {
				color: #a3a3a3;
				display: block;
			}

			&:hover a {
				color: @hover-color;
			}

			&::after {
				position: absolute;
				left: -20px;
				top: 1px;
				content: "\e600";
				font-size: 2rem;
				color: #999;
			}
		}
	}

	&-div3 {
		ul {
			margin-top: 1rem;
		}

		li:extend(.f-toe) {
			padding: 0.5rem 0;
			color: #a3a3a3;

			i {
				font-size: 2.4rem;
				float: left;
				height: 26px;
				line-height: 30px;
				margin-right: 0.3rem;
			}

			em {
				font-family: iconfont;
				width: 4rem;
				height: 4rem;
				border-radius: 50%;
				color: #fff;
				background: #727272;
				display: block;
				font-size: 3rem;
				line-height: 4rem;
				text-align: center;
				float: left;
				margin-right: 0.5rem;
			}

			b {
				height: 3rem;
				line-height: 3.2rem;
			}
		}
	}

	&-bottom {
		padding: 2rem 1rem;
		border-top: 1px solid #eaeaea;
		overflow: hidden;

		&-link {
			& > div {
				padding: 0;
			}

			span {
				display: block;
				text-align: right;
				color: #999;
			}

			strong {

				a {
					color: #999;
					margin-right: 0.8rem;

					&:hover {
						color: @hover-color;
					}
				}
			}
		}
	}

	h2, h3 {
		text-align: center;
		color: #999;
		font-size: 1.5rem;
	}
}


@media only screen and (max-width:@breakpoint-medium-max) {
	.hd1 {
		padding: 1.5rem 1rem;

		&-left {
			img {
				height: 30px;
			}
		}
	}

	.ads img {
		height: 8rem;
	}

	.user {
		margin-top: 3rem !important;

		&-div {
			padding: 0 2rem;
		}
	}

	.hd {
		&-search {
			padding: 1.5rem 1rem;

			&-left {

				img {
					height: 30px;
				}
			}
		}

		&-bottom {
			&-nav {
				display: none;
				float: left;
				width: 100%;
				margin: 0;
				background: #fff;
				position: fixed;
				top: -10px;
				left: 0;
				z-index: 9999999999;
			}

			ul {
				background: #ececec;
			}

			li {
				width: 100%;
				border-bottom: 1px solid #e2e2e2;
				padding: 1rem;
			}

			h1 {
				padding: 1.5rem;
				position: relative;

				input {
					font-size: 1.6rem;
					border: 1px solid #bababa;
					padding: 6px 50px 6px 15px;
					border-radius: 17px;
					width: 90%;
				}

				em {
					position: absolute;
					color: #bfbfbf;
					font-size: 23px;
					right: 18%;
					top: 18px;
				}

				i {
					color: #3d97d7;
					font-size: 3.2rem;
					height: 40px;
					line-height: 40px;
					position: absolute;
					right: 1.5rem;
					top: 1.5rem;
				}
			}
		}
	}

	.comarticle-right {
		margin: 0 2rem;
	}

	.comarticle .am-offcanvas-bar {
		background: #f1f1f1;
		margin: 0;
		border-right: 1px solid #dedede;

		&::after {
			background: transparent;
		}
	}
}

@media @large-only {
	.comarticle .am-offcanvas {
	display:block;
	}
	.cg {
		max-width: 900px;
	}

	.hd-bottom-nav {
		display: block;
	}
}
